/**
 * @file resilientParse.ts
 * @description  Babel 
 *
 * @babel/parser  errorRecovery  **parser ** 
 *  **tokenizer ** 
 *
 *  tokenizer 
 * 1. errorRecovery: true
 * 2.  tokenizer mask    N 
 * 3. 
 *
 *  AST
 */
import * as parser from '@babel/parser';
import type { ParserOptions } from '@babel/parser';
import type { File } from '@babel/types';

/**  */
const MAX_FIX_RETRIES = 15;

/**
 *  Babel 
 *
 *  parser.parse()  tokenizer 
 *  mask 
 *
 * @param content   JS/TS 
 * @param options   @babel/parser errorRecovery 
 * @returns         ASTFile 
 */
export function resilientParse(
    content: string,
    options?: Partial<ParserOptions>
): File {
    const parseOptions: ParserOptions = {
        sourceType: 'module',
        plugins: [
            'jsx', 
            'typescript', 
            'decorators-legacy', 
            'classProperties', 
            'classPrivateProperties', 
            'classPrivateMethods',
            'topLevelAwait',
            'asyncGenerators',
            'dynamicImport',
            'objectRestSpread'
        ],
        ...options,
        errorRecovery: true, // 
    };

    let currentContent = content;
    const maskedLines = new Set<number>();

    for (let attempt = 0; attempt <= MAX_FIX_RETRIES; attempt++) {
        try {
            return parser.parse(currentContent, parseOptions);
        } catch (error: any) {
            const lineNum = extractErrorLine(error);

            //  or  mask   
            if (lineNum === null || maskedLines.has(lineNum)) {
                throw error;
            }

            maskedLines.add(lineNum);

            // 
            if (maskedLines.size <= 5) {
                const snippet = (error.message || '').substring(0, 80);
                console.log(`[resilientParse]  ${lineNum}  (attempt ${attempt + 1}): ${snippet}`);
            }

            currentContent = maskLineInContent(currentContent, lineNum);
        }
    }

    // fallback throw
    throw new Error(`[resilientParse]  (${MAX_FIX_RETRIES})`);
}

//  helpers 

/**
 *  babel SyntaxError 1-based
 */
function extractErrorLine(error: any): number | null {
    // babel  .loc.line
    if (error?.loc?.line && typeof error.loc.line === 'number') {
        return error.loc.line;
    }
    //  message  "(line:column)"
    const match = error?.message?.match(/\((\d+):\d+\)/);
    return match ? parseInt(match[1], 10) : null;
}

/**
 * 1-based
 */
function maskLineInContent(content: string, line: number): string {
    const lines = content.split('\n');
    const idx = line - 1;
    if (idx >= 0 && idx < lines.length) {
        lines[idx] = ' '.repeat(lines[idx].length);
    }
    return lines.join('\n');
}
