/**
 * 
 */
import * as vscode from 'vscode';

/**
 * 
 */
export function compressMultipleLines() {
    const editor = vscode.window.activeTextEditor;
    if (!editor) {
        vscode.window.showErrorMessage('');
        return;
    }

    const selection = editor.selection;
    if (selection.isEmpty) {
        vscode.window.showInformationMessage('');
        return;
    }

    const selectedText = editor.document.getText(selection);
    const document = editor.document;
    
    // 
    if (isCommentContent(selectedText)) {
        const compressedText = compressComments(selectedText);
        // 
        editor.edit(editBuilder => {
            editBuilder.replace(selection, compressedText);
        }).then(success => {
            if (success) {
                vscode.window.showInformationMessage('');
            } else {
                vscode.window.showErrorMessage('');
            }
        });
        return;
    }
    
    // 
    const languageId = document.languageId;
    let compressedText = '';

    try {
        switch (languageId) {
            case 'html':
            case 'xml':
                compressedText = compressHtml(selectedText);
                break;
            case 'javascript':
            case 'typescript':
            case 'vue':
                compressedText = compressJavaScript(selectedText);
                break;
            case 'json':
            case 'jsonc':
                compressedText = compressJson(selectedText);
                break;
            case 'css':
            case 'scss':
            case 'sass':
            case 'less':
                compressedText = compressCss(selectedText);
                break;
            default:
                // 
                compressedText = compressGeneric(selectedText);
                break;
        }

        // 
        editor.edit(editBuilder => {
            editBuilder.replace(selection, compressedText);
        }).then(success => {
            if (success) {
                vscode.window.showInformationMessage(` ${languageId} `);
            } else {
                vscode.window.showErrorMessage('');
            }
        });

    } catch (error) {
        console.error('[Compress Lines] Error:', error);
        vscode.window.showErrorMessage('');
    }
}

/**
 * 
 */
function isCommentContent(text: string): boolean {
    const trimmedText = text.trim();
    
    // JavaScript/TypeScript/CSS 
    if (trimmedText.startsWith('/*') && trimmedText.endsWith('*/')) {
        return true;
    }
    
    // HTML 
    if (trimmedText.startsWith('<!--') && trimmedText.endsWith('-->')) {
        return true;
    }
    
    // 
    const lines = text.split('\n');
    const nonEmptyLines = lines.filter(line => line.trim() !== '');
    
    if (nonEmptyLines.length === 0) {
        return false;
    }
    
    // JavaScript/TypeScript 
    if (nonEmptyLines.every(line => line.trim().startsWith('//'))) {
        return true;
    }
    
    // Python/Shell 
    if (nonEmptyLines.every(line => line.trim().startsWith('#'))) {
        return true;
    }
    
    // SQL 
    if (nonEmptyLines.every(line => line.trim().startsWith('--'))) {
        return true;
    }
    
    return false;
}

/**
 * 
 */
function compressComments(text: string): string {
    const trimmedText = text.trim();
    
    //  JavaScript/TypeScript/CSS 
    if (trimmedText.startsWith('/*') && trimmedText.endsWith('*/')) {
        const content = trimmedText.slice(2, -2).trim();
        const compressedContent = content
            .replace(/^\s*\*/gm, '') //  *
            .replace(/\s+/g, ' ') // 
            .trim();
        return `/* ${compressedContent} */`;
    }
    
    //  HTML 
    if (trimmedText.startsWith('<!--') && trimmedText.endsWith('-->')) {
        const content = trimmedText.slice(4, -3).trim();
        const compressedContent = content
            .replace(/\s+/g, ' ') // 
            .trim();
        return `<!-- ${compressedContent} -->`;
    }
    
    // 
    const lines = text.split('\n');
    const nonEmptyLines = lines.filter(line => line.trim() !== '');
    
    if (nonEmptyLines.length === 0) {
        return text;
    }
    
    // JavaScript/TypeScript 
    if (nonEmptyLines.every(line => line.trim().startsWith('//'))) {
        const content = nonEmptyLines
            .map(line => line.trim().replace(/^\/\/\s*/, ''))
            .join(' ')
            .replace(/\s+/g, ' ')
            .trim();
        return `// ${content}`;
    }
    
    // Python/Shell 
    if (nonEmptyLines.every(line => line.trim().startsWith('#'))) {
        const content = nonEmptyLines
            .map(line => line.trim().replace(/^#\s*/, ''))
            .join(' ')
            .replace(/\s+/g, ' ')
            .trim();
        return `# ${content}`;
    }
    
    // SQL 
    if (nonEmptyLines.every(line => line.trim().startsWith('--'))) {
        const content = nonEmptyLines
            .map(line => line.trim().replace(/^--\s*/, ''))
            .join(' ')
            .replace(/\s+/g, ' ')
            .trim();
        return `-- ${content}`;
    }
    
    // 
    return compressGeneric(text);
}

/**
 * HTML 
 */
function compressHtml(text: string): string {
    return text
        // 
        .replace(/>\s+</g, '><')
        // 
        .replace(/^\s+|\s+$/gm, '')
        // 
        .replace(/\s+/g, ' ')
        // 
        .replace(/<!--[\s\S]*?-->/g, '')
        .trim();
}

/**
 * JavaScript/TypeScript 
 */
function compressJavaScript(text: string): string {
    return text
        // 
        .replace(/\/\/.*$/gm, '')
        // 
        .replace(/\/\*[\s\S]*?\*\//g, '')
        // 
        .replace(/^\s+|\s+$/gm, '')
        // 
        .replace(/\n\s*\n/g, '\n')
        // 
        .replace(/([;{}])\s*(?=\S)/g, '$1 ')
        // 
        .replace(/\s+/g, ' ')
        .trim();
}

/**
 * JSON 
 */
function compressJson(text: string): string {
    try {
        //  JSON
        const parsed = JSON.parse(text);
        return JSON.stringify(parsed);
    } catch (error) {
        //  JSON
        return compressGeneric(text);
    }
}

/**
 * CSS 
 */
function compressCss(text: string): string {
    return text
        // 
        .replace(/\/\*[\s\S]*?\*\//g, '')
        // 
        .replace(/^\s+|\s+$/gm, '')
        // 
        .replace(/\n\s*\n/g, '\n')
        // 
        .replace(/\s*{\s*/g, '{')
        .replace(/\s*}\s*/g, '}')
        // 
        .replace(/\s*:\s*/g, ':')
        // 
        .replace(/;\s*/g, ';')
        // 
        .replace(/\s+/g, ' ')
        .trim();
}

/**
 * 
 */
function compressGeneric(text: string): string {
    return text
        // 
        .replace(/^\s+|\s+$/gm, '')
        // 
        .replace(/\n\s*\n/g, '\n')
        // 
        .replace(/\s+/g, ' ')
        .trim();
}
